$body-font: Times, "Times New Roman", sans;
$header-font: "helvetica neue", Helvetica, Arial, sans-serif;
$main-col-width: 48rem;

html, body{
  margin:0;
  width: 100%;
  height: 100%;
  font-size: 1em;
}

hr { 
  display: block; 
  height: 1px;
  border: 0; 
  border-top: 1px solid #000;
  margin: 1em 0; 
  padding: 0; 
  position:fixed;
  top:53px;
  width:100%;
  left:0px
}

.wrapper{
  max-width: $main-col-width;
  margin:0 auto;
}
body {
  background-color: white;
  font-family: $body-font;
}
h1, h2{
  font-size:2em;
  font-family: $header-font

}
h2, h3{
  font-family:sans-serif;
  color:red;
}
h3{
  font-size:1.5em;
}

h1{
  text-transform:uppercase;
  font-weight:normal;
}
h1, nav ul{
  display:inline-block;
}
a{
  color:blue;
}
.current {
  // background-color: black;
  color:red;
}
figure{
  margin:0;
}
figure img{
  width:100%;
  height:100%;
}
#logo{
  margin-inline-start:0;
}
header {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: white;
  // border-bottom: 2px solid black;
  display: flex;
  // flex-direction:column;
  // max-width: $main-col-width;
  justify-content: space-between;
}
article, h1{
  margin:1rem 
}
article{
  margin-top:7em 
}
article p {
  font-size: 1.5em;
  line-height: 1.5em;
  margin-bottom: 1em;
}
nav + article {
  margin-top: 5em;
}
nav {
  background-color:rgba(255,255,255,0.9);
}
nav ul{
  display: flex;
  list-style: none;
  margin-top:1.7em;
}
nav ul li{
  display: block;
  width: 7em;
  font-size:2em 
}
nav a{
  text-decoration:none;
}
@media (min-width: 850px){
  header{
      flex-direction:row;
 }
}
